import {
  Avatar,
  Box,
  Checkbox,
  colors,
  Hidden,
  IconButton,
  makeStyles,
  Tooltip,
  Typography
} from '@material-ui/core';
import LabelImportantIcon from '@material-ui/icons/LabelImportant';
import LabelImportantOutlinedIcon from '@material-ui/icons/LabelImportantOutlined';
import StarIcon from '@material-ui/icons/Star';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import clsx from 'clsx';
import moment from 'moment';
import PropTypes from 'prop-types';
import React, { ChangeEvent, FC } from 'react';
import { Link as RouterLink, useParams } from 'react-router-dom';
import { useSelector } from 'src/store';
import { Theme } from 'src/theme';
import { Mail } from 'src/types/mail';
import getInitials from 'src/utils/getInitials';

interface MailItemProps {
  className?: string;
  mail: Mail;
  onDeselect?: () => void;
  onSelect?: () => void;
  selected: boolean;
}

const getTo = (params: any, mailId: string): string => {
  const { systemLabel, customLabel } = params;
  const baseUrl = '/app/mail';

  if (systemLabel) {
    return `${baseUrl}/${systemLabel}/${mailId}`;
  }

  if (customLabel) {
    return `${baseUrl}/label/${customLabel}/${mailId}`;
  }

  return baseUrl;
};

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    backgroundColor: theme.palette.background.default,
    padding: theme.spacing(2),
    borderBottom: `1px solid ${theme.palette.divider}`,
    display: 'flex',
    alignItems: 'center',
    '&:hover': {
      backgroundColor: theme.palette.action.hover
    }
  },
  unread: {
    position: 'relative',
    '&:before': {
      content: '" "',
      height: '100%',
      position: 'absolute',
      left: 0,
      top: 0,
      width: 4,
      backgroundColor: theme.palette.error.main
    },
    '& $name, & $subject': {
      fontWeight: theme.typography.fontWeightBold
    }
  },
  selected: {
    backgroundColor: theme.palette.action.selected
  },
  filterActive: {
    color: colors.amber[400]
  },
  content: {
    cursor: 'pointer',
    textDecoration: 'none'
  },
  details: {
    [theme.breakpoints.up('md')]: {
      display: 'flex',
      alignItems: 'center',
      flexGrow: 1
    }
  },
  name: {
    [theme.breakpoints.up('md')]: {
      minWidth: 180,
      flexBasis: 180
    }
  },
  subject: {
    maxWidth: 400,
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis'
  },
  message: {
    maxWidth: 800,
    flexGrow: 1,
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    marginRight: 'auto'
  },
  label: {
    fontFamily: theme.typography.fontFamily,
    fontSize: theme.typography.pxToRem(12),
    color: theme.palette.common.white,
    paddingLeft: 4,
    paddingRight: 4,
    paddingTop: 2,
    paddingBottom: 2,
    borderRadius: 2,
    '& + &': {
      marginLeft: theme.spacing(1)
    }
  },
  date: {
    whiteSpace: 'nowrap'
  }
}));

const MailItem: FC<MailItemProps> = ({
  className,
  mail,
  onDeselect,
  onSelect,
  selected,
  ...rest
}) => {
  const classes = useStyles();
  const params = useParams();
  const { labels } = useSelector(state => state.mail);

  const handleCheckboxChange = (event: ChangeEvent<HTMLInputElement>): void =>
    event.target.checked ? onSelect() : onDeselect();

  const handleStarToggle = (): void => {
    // dispatch action
  };

  const handleImportantToggle = (): void => {
    // dispatch action
  };

  const to = getTo(params, mail.id);

  return (
    <div
      className={clsx(
        classes.root,
        {
          [classes.unread]: !mail.isUnread,
          [classes.selected]: selected
        },
        className
      )}
      {...rest}
    >
      <Hidden smDown>
        <Box mr={1} display="flex" alignItems="center">
          <Checkbox checked={selected} onChange={handleCheckboxChange} />
          <Tooltip title="Starred">
            <IconButton onClick={handleStarToggle}>
              {mail.isStarred ? (
                <StarIcon className={classes.filterActive} />
              ) : (
                <StarBorderIcon />
              )}
            </IconButton>
          </Tooltip>
          <Tooltip title="Important">
            <IconButton onClick={handleImportantToggle}>
              {mail.isImportant ? (
                <LabelImportantIcon className={classes.filterActive} />
              ) : (
                <LabelImportantOutlinedIcon />
              )}
            </IconButton>
          </Tooltip>
        </Box>
      </Hidden>
      <Box
        minWidth="1px"
        display="flex"
        flexGrow={1}
        component={RouterLink}
        // @ts-ignore
        to={to}
        className={classes.content}
      >
        <Avatar src={mail.from.avatar}>{getInitials(mail.from.name)}</Avatar>
        <Box minWidth="1px" ml={1} className={classes.details}>
          <Typography
            variant="body2"
            color="textPrimary"
            className={classes.name}
          >
            {mail.from.name}
          </Typography>
          <Typography
            variant="body2"
            color="textSecondary"
            className={classes.subject}
          >
            {mail.subject}
          </Typography>
          <Hidden smDown>
            <Typography
              variant="body2"
              color="textSecondary"
              className={classes.message}
            >
              <Box component="span" ml={2}>
                -
              </Box>
              {mail.message}
            </Typography>
            {mail.labelIds.length > 0 && (
              <Box display="flex" mx={2}>
                {mail.labelIds.map((labelId: string) => {
                  const label = labels.find(_label => _label.id === labelId);

                  if (!label) return null;

                  return (
                    <span
                      style={{ backgroundColor: label.color }}
                      key={label.id}
                      className={classes.label}
                    >
                      {label.name}
                    </span>
                  );
                })}
              </Box>
            )}
          </Hidden>
          <Typography
            className={classes.date}
            color="textSecondary"
            variant="caption"
          >
            {moment(mail.createdAt).format('DD MMM YYYY')}
          </Typography>
        </Box>
      </Box>
    </div>
  );
};

MailItem.propTypes = {
  className: PropTypes.string,
  // @ts-ignore
  mail: PropTypes.object.isRequired,
  onDeselect: PropTypes.func,
  onSelect: PropTypes.func,
  selected: PropTypes.bool.isRequired
};

MailItem.defaultProps = {
  onDeselect: () => {},
  onSelect: () => {}
};

export default MailItem;
