import {
  Backdrop,
  Box,
  Button,
  Divider,
  IconButton,
  Input,
  makeStyles,
  Paper,
  Portal,
  SvgIcon,
  Tooltip,
  Typography
} from '@material-ui/core';
import AddPhotoIcon from '@material-ui/icons/AddPhotoAlternate';
import AttachFileIcon from '@material-ui/icons/AttachFile';
import clsx from 'clsx';
import React, { FC, useState } from 'react';
import {
  Maximize as MaximizeIcon,
  Minimize as MinimizeIcon,
  X as XIcon
} from 'react-feather';
import QuillEditor from 'src/components/QuillEditor';
import { closeCompose } from 'src/slices/mail';
import { useDispatch, useSelector } from 'src/store';
import { Theme } from 'src/theme';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    maxWidth: `calc(100% - ${theme.spacing(6)}px)`,
    maxHeight: `calc(100% - ${theme.spacing(6)}px)`,
    width: 600,
    position: 'fixed',
    bottom: 0,
    right: 0,
    margin: theme.spacing(3),
    outline: 'none',
    zIndex: 2000,
    display: 'flex',
    flexDirection: 'column',
    minHeight: 500
  },
  fullScreen: {
    height: '100%',
    width: '100%'
  },
  input: {
    width: '100%'
  },
  editor: {
    flexGrow: 1,
    '& .ql-editor': {
      minHeight: 300
    }
  },
  action: {
    marginRight: theme.spacing(1)
  }
}));

const Compose: FC = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { isComposeOpen } = useSelector(state => state.mail);
  const [fullScreen, setFullScreen] = useState<boolean>(false);
  const [messageBody, setMessageBody] = useState<string>('');

  const handleChange = (value: string): void => {
    setMessageBody(value);
  };

  const handleExitFullScreen = (): void => {
    setFullScreen(false);
  };

  const handleEnterFullScreen = (): void => {
    setFullScreen(true);
  };

  const handleClose = (): void => {
    dispatch(closeCompose());
  };

  if (!isComposeOpen) {
    return null;
  }

  return (
    <Portal>
      <Backdrop open={fullScreen} />
      <Paper
        className={clsx(classes.root, { [classes.fullScreen]: fullScreen })}
        elevation={12}
      >
        <Box
          bgcolor="background.dark"
          display="flex"
          alignItems="center"
          py={1}
          px={2}
        >
          <Typography variant="h5" color="textPrimary">
            New Message
          </Typography>
          <Box flexGrow={1} />
          {fullScreen ? (
            <IconButton onClick={handleExitFullScreen}>
              <SvgIcon fontSize="small">
                <MinimizeIcon />
              </SvgIcon>
            </IconButton>
          ) : (
            <IconButton onClick={handleEnterFullScreen}>
              <SvgIcon fontSize="small">
                <MaximizeIcon />
              </SvgIcon>
            </IconButton>
          )}
          <IconButton onClick={handleClose}>
            <SvgIcon fontSize="small">
              <XIcon />
            </SvgIcon>
          </IconButton>
        </Box>
        <Box p={2}>
          <Input className={classes.input} disableUnderline placeholder="To" />
          <Input
            className={classes.input}
            disableUnderline
            placeholder="Subject"
          />
        </Box>
        <Divider />
        <QuillEditor
          className={classes.editor}
          onChange={handleChange}
          placeholder="Leave a message"
          value={messageBody}
        />
        <Divider />
        <Box display="flex" alignItems="center" py={1} px={2}>
          <Button
            color="secondary"
            variant="contained"
            className={classes.action}
          >
            Send
          </Button>
          <Tooltip title="Attach image">
            <IconButton size="small" className={classes.action}>
              <AddPhotoIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="Attach file">
            <IconButton size="small" className={classes.action}>
              <AttachFileIcon />
            </IconButton>
          </Tooltip>
        </Box>
      </Paper>
    </Portal>
  );
};

export default Compose;
