import {
  Box,
  Button,
  Divider,
  Drawer,
  Hidden,
  List,
  makeStyles
} from '@material-ui/core';
import PropTypes from 'prop-types';
import React, { FC, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { closeSidebar, openCompose } from 'src/slices/mail';
import { useDispatch, useSelector } from 'src/store';
import LabelItem from './LabelItem';

interface SidebarProps {
  containerRef: any;
}

const useStyles = makeStyles(() => ({
  drawerDesktopRoot: {
    width: 280,
    flexShrink: 0
  },
  drawerDesktopPaper: {
    position: 'relative'
  },
  drawerMobilePaper: {
    position: 'relative',
    width: 280
  },
  drawerMobileBackdrop: {
    position: 'absolute'
  }
}));

const Sidebar: FC<SidebarProps> = ({ containerRef }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const location = useLocation();
  const { labels, isSidebarOpen } = useSelector(state => state.mail);

  const handleCloseSidebar = (): void => {
    dispatch(closeSidebar());
  };

  const handleComposeClick = (): void => {
    dispatch(openCompose());
  };

  useEffect(() => {
    if (isSidebarOpen) {
      dispatch(closeSidebar());
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.pathname]);

  const content = (
    <div>
      <Box px={3} py={2}>
        <Button
          color="secondary"
          fullWidth
          variant="contained"
          onClick={handleComposeClick}
        >
          Compose
        </Button>
      </Box>
      <Divider />
      <Box py={2} pr={2}>
        <List>
          {labels.map(label => (
            <LabelItem key={label.id} label={label} />
          ))}
        </List>
      </Box>
    </div>
  );

  return (
    <>
      <Hidden smDown>
        <Drawer
          variant="permanent"
          classes={{
            root: classes.drawerDesktopRoot,
            paper: classes.drawerDesktopPaper
          }}
        >
          {content}
        </Drawer>
      </Hidden>
      <Hidden mdUp>
        <Drawer
          variant="temporary"
          open={isSidebarOpen}
          onClose={handleCloseSidebar}
          classes={{
            paper: classes.drawerMobilePaper
          }}
          style={{ position: 'absolute', zIndex: 1200 }}
          BackdropProps={{ classes: { root: classes.drawerMobileBackdrop } }}
          ModalProps={{ container: () => containerRef.current }}
        >
          {content}
        </Drawer>
      </Hidden>
    </>
  );
};

Sidebar.propTypes = {
  containerRef: PropTypes.any.isRequired
};

export default Sidebar;
